import Vue from 'vue';
import Vuex from 'vuex';
import publicFun from '@/assets/publicFunction';
Vue.use(Vuex);
/**
 * 设置全局访问的state对象
 */
const state={
    show:false,// 测试
 
}

/**
 * 实时监听state里面值的变法(最新变法)
 */
const getters={
    getShow(state){
        return state.show
    },

}
/**
 * 设置对外可调用的方法
 */
const mutations = {
    newShow(state,val){
        state.show = val
    }
}

/**
 * 这里面的方法是用来异步触发mutations里面的方法
 */
const actions={
    SETSHOW(context,val){
        context.commit('newShow',val)
    },
}
const store =new Vuex.Store({
    state,
    getters,
    mutations,
    actions
});
export default store;


