import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)
const route = new Router({
  routes: [{
      path: '/',
      name: 'home',
      component: resolve => require(['@/pages/home/home'], resolve)
    },
    {
      path: '/advantage',
      name: 'advantage',
      component: resolve => require(['@/pages/advantagePage/advantagePage'], resolve)
    },
    {
      path: '/partner',
      name: 'partner',
      component: resolve => require(['@/pages/partnerPage/partnerPage'], resolve)
    },

    {
      path: '/about',
      name: 'about',
      component: resolve => require(['@/pages/aboutPage/about'], resolve)
    },
    {
      path: '/customized',
      name: 'customized',
      component: resolve => require(['@/pages/customizedPage/customized'], resolve)
    },
    {
      path: '/product',
      name: 'product',
      component: resolve => require(['@/pages/productPage/product'], resolve)
    },
  ],
  //回到顶部
  scrollBehavior(to,from,savedPosition){
    if(savedPosition){
      return savedPosition;
    }else{
      return {x:0,y:0}
    }
  },
})

/**
 * 前置守卫
 */
route.beforeEach((to, from, next) => {
  //to.query.isTrue =true;
  //console.log(from);
  /**
   * 拦截头部导航页面跳转
   */
  if (to.path == '/StRollBallpage') {
    to.query.isTrue = true;
    //console.log(to);
  }
  next();

})

export default route



